import { useEffect, useState } from 'react'
import { Helmet } from 'react-helmet-async'
import { useParams } from 'react-router'
import { useSetRecoilState } from 'recoil'

import { Box, Card, Chip, Container, Grid, Stack, Typography, useMediaQuery, useTheme } from '@mui/material'
import '@toast-ui/editor/dist/toastui-editor.css'

import { formatDate } from 'src/utils/formatDateTime'

import { ClubInfo } from 'src/components/ClubInfo'
import PageTitleWrapper from 'src/components/PageTitleWrapper'
import SuspenseLoader from 'src/components/SuspenseLoader'

import { useDeleteLikeClub, useGetClubInformation, usePostLikeClub } from 'src/hooks/api/club'
import { useGetConferenceRound } from 'src/hooks/api/clubConferenceHooks'
import { isLikeBtnClickedAtom } from 'src/recoil/atom'

const ConferencePage: React.FC = () => {
  const { id, roundId } = useParams()
  const clubId = Number(id)
  const roundIdNum = Number(roundId)

  const theme = useTheme()
  const isDesktopOrTablet = useMediaQuery(theme.breakpoints.up('sm'))

  const [isLike, setIsLike] = useState(false)
  const setIsLikeBtnClicked = useSetRecoilState(isLikeBtnClickedAtom)

  const { data: club, isFetched: isClubFetched } = useGetClubInformation(clubId)
  const { data: conferenceRound, isLoading: isConferenceLoading } = useGetConferenceRound(clubId, roundIdNum)

  const postLikeClubMutation = usePostLikeClub(clubId)
  const deleteLikeClubMutation = useDeleteLikeClub(clubId)

  useEffect(() => {
    if (isClubFetched && club) {
      setIsLike(club.isliked)
    }
  }, [isClubFetched, club])

  const handleLike: React.MouseEventHandler<HTMLButtonElement> = async () => {
    try {
      if (isLike) {
        await deleteLikeClubMutation.mutateAsync()
      } else {
        await postLikeClubMutation.mutateAsync()
      }
      setIsLike(!isLike)
      setIsLikeBtnClicked(true)
    } catch (error) {}
  }

  const startDateLabel = conferenceRound ? formatDate(new Date(conferenceRound.startAt)) : ''

  return (
    <>
      <Helmet>
        <title>{conferenceRound?.conferenceTitle ?? 'Conference'}</title>
      </Helmet>

      <PageTitleWrapper>
        <Grid container>
          {club && (
            <ClubInfo club={club} handleLike={handleLike} isLike={isLike} isDesktopOrTablet={isDesktopOrTablet} />
          )}
        </Grid>
      </PageTitleWrapper>

      <Container maxWidth="md">
        {isConferenceLoading ? (
          <SuspenseLoader />
        ) : conferenceRound ? (
          <Card sx={{ px: 10, py: 5 }}>
            <Stack spacing={2}>
              <Typography sx={{ fontSize: '20px', fontWeight: 'bold' }} overflow="auto">
                {conferenceRound.conferenceTitle}
              </Typography>

              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <Typography variant="body2" color="text.secondary">
                  {startDateLabel}
                </Typography>
                <Box sx={{ display: 'flex', gap: 0.75 }}>
                  {conferenceRound.categories.map((category, index) => (
                    <Chip key={index} label={category} size="small" variant="filled" />
                  ))}
                </Box>
              </Box>

              <Box sx={{ mt: 1 }}>
                <div
                  className="toastui-editor-contents"
                  dangerouslySetInnerHTML={{ __html: conferenceRound.note }}
                ></div>
              </Box>
            </Stack>
          </Card>
        ) : (
          <Card>
            <Box height="40vh" display="flex" justifyContent="center" alignItems="center">
              <Typography variant="h6">활동 기록을 찾을 수 없어요.</Typography>
            </Box>
          </Card>
        )}
      </Container>
    </>
  )
}

export default ConferencePage
